export default {
	name: 'rx-news',
	props:{
		newsItem:{
			type:Object
		}
	},
	methods:{
	
		imgReqPath(val){
			if(''!=val){
				let url = _baseUrl + "/sys/core/file/previewImage.do?fileId=" + val;
				return url;
			}				
		},
		getAbstractNews(content){
			if(content.length>100){	
				let subContent = content.substr(content.indexOf('>')+1,100);
				return subContent + "...";
			}
			return content;
		}
	}
}



