import router from '@/router/index';
export default{
	name:'my-info',
	data(){
		return{
			data: [],
			mainDept:'',
			photo:""
		}
	},
	created(){
		this.getInfo();
	},
	methods:{
		getInfo() {
			var url = _baseUrl + '/mobile/oa/personalInfo/get.do';
			this.$ajax.get(url).then(response => {					
				this.data = response.data.user;
				this.photo=response.data.user.photo;
				if(response.data.mainDept == null){
					this.mainDept = '无';
				}else{
					this.mainDept = response.data.mainDept.name;
				}
			})
		},
		loginOut(){
			router.push({name:'login',params:{source:'loginOut'}});
		},
		getFilePath(fileId) {
			return _baseUrl + "/sys/core/file/previewFile.do?fileId=" + fileId;
		},
	}
	
}



