import Vue from 'vue';
import router from '@/router/index'
import { Grids } from 'vue-ydui/dist/lib.rem/grids';
import { Slider, SliderItem } from 'vue-ydui/dist/lib.rem/slider';
Vue.component(Slider.name, Slider);
Vue.component(SliderItem.name, SliderItem);

export default {
	name:'work-space',
	data() {
		return {
			newsList:[],
			newdate: new Date()
		}
	},
	created(){
		this.getInsNewsImage();
		this.$store.dispatch('getCountNum',this);//vuex管理数据
	},
	computed:{
		count(){
			return this.$store.state.pageModule.countNum;//读取Vuex里面的数据
		}
	},
	methods: {
		logoutConfirm() {
			this.$dialog.confirm({
				title: '注销',
				mes: '是否退出登录',
				opts: [{
						txt: "确定", //按钮文字
						color: "green", //按钮颜色
						stay: false, //是否保留提示框
						callback: () => {
							router.push({
								name: 'login'
							});
						}
					},
					{
						txt: "取消", //按钮文字
						color: false, //按钮颜色
						stay: false, //是否保留提示框							  
					}
				]
			});
		},
		getInsNewsImage(){
			var url = _baseUrl + "/mobile/oa/getInsNewsImage.do";
			this.$ajax.get(url).then(response => {
				this.newsList = response.data.data;
			});
		},
		getImageReqPath(fileId){
			console.info(fileId);
			let url = _baseUrl + "/sys/core/file/previewImage.do?fileId=";
			if(fileId!='undefined'){					
				return url + fileId;
			}
		},
		toNewsDetails(newsItem){
			this.$router.push({name:'insNewsDetail',params:{newsItem:newsItem}})
		},
		getTime(value){
			var newdate = new Date();
			if( value === 'gatYear'){
				return newdate.getMonth()+1;
			}else if( value === 'getDay' ){
				return (newdate.getMonth()+1)+'月'+ newdate.getDate()+'日';
			}
		}
		
	}

}



