<template>
	<div class="contentBox">
		<yd-navbar 
			slot="navbar" 
			title="公告" 
		>
		</yd-navbar>
		<!--<scroller 
			class="scroller"
			ref="myScroller"
			:on-refresh="refresh"
		>-->
			<dl class="scroll">
				<rx-news 
					v-for="newsItem in newsList" 
					:key="newsItem.pkId"  
					:newsItem="newsItem" 
					@click.native="getNewsDetals(newsItem)"
				></rx-news>
			</dl>
			<span></span>
		<!--</scroller>-->
	
	</div>
</template>

<script>
	import Vue from 'vue';
	import router from '@/router/index';
	import listItem from '@/components/common/ListItem';
	import rxNews from '@/components/common/rxNews';
	Vue.component(listItem.name, listItem);
	Vue.component(rxNews.name, rxNews);
	
	export default {
		name:'ins-news',
		data() {
			return {
				newsList: [],
				Lock:false
			}
		},
	    props:{
			bv:Boolean
		},
		/*watch:{
			lockScroller:{
				handler:function(vel){
					this.Lock = !vel;
					console.log(vel)
				}
			}
		},*/
		methods: {
			getAllNews() {
				var url = _baseUrl + "/mobile/oa/getAllNews.do";
				
				this.$ajax({
					url: url,
					method: 'POST',
				}).then(response => {
					this.newsList = response.data.data;
				}).catch(error => {
					console.log(error);
				});
			},
			getNewsDetals(newsItem){
				this.$router.push({name:'insNewsDetail',params:{'newsItem':newsItem}});
			},
			refresh(){
				console.log(this.Lock)
			}

		},
		created() {
			this.getAllNews();
		}

	}
</script>

<style scoped>

.scroller{
	margin-top: 1rem;
}

.scroller >>> ._v-content{
	padding-bottom: 1rem;
}

.contentBox{
	height: 100%;
}

.contentBox dl{
	height: calc(100% - 1rem);
	overflow-y:auto ;
	overflow-x: hidden;
}

</style>


