<template>
    <move :listData="iconList" :doubleIcon="true">
    	<div slot="page1" class="pageBox">
    		<work-space></work-space>
    	</div>
    	<div slot="page2" class="pageBox">
    		<ins-news></ins-news>
    	</div>
    	<div slot="page3" class="pageBox">
    		<persona></persona>
    	</div>
    	<div slot ="page4" class="pageBox">
    		<my-info></my-info>
    	</div>
    </move>
</template>
<script type="text/ecmascript-6">
import WorkSpace from '@/components/oa/WorkSpace';
import insNews from '@/components/oa/insNews';
import persona from '@/components/oa/personalAddrBookIndex';
import myInfo from '@/components/oa/myInfo';
import Move from '@/components/Move';
export default {
	data() {
		return {
			iconList:{
				icon:{
					1:"workspace",
					2:'notice',
					3:'department',
					4:'account',
				},
				title:{
					1:'工作台',
					2:'公告',
					3:'通讯录',
					4:'我'
				}
			}
		}
	},
	components:{
	  	WorkSpace,
	  	insNews,
	  	persona,
	  	myInfo,
	  	Move
	},
	methods:{
		
	}
}
</script>

<style scoped>

</style>



