<template>
	<dd>
		<ul class="text-container">
			<li class="text-title">
				<h1 slot="title" class="text-title">{{newsItem.subject}}</h1>	
			</li>	
			<li class="text-author">
				<span slot="author" class="text-author">发布人：{{newsItem.author != null ? newsItem.author :'匿名'}}</span>
				<span slot="createTime" class="text-time">{{newsItem.updateTime}}</span>
			</li>

			<li v-if="newsItem.imgFileId!=''" class="text-img" >
				<img name="img" :src="imgReqPath(newsItem.imgFileId)" ></img>
				<span 
					class="text-keywords"
					v-html="newsItem.keywords"
				></span>
			</li>
			
			<li class="text-content" v-else>
				<p 
					slot="content" 
					class="text-content" 
					v-html="getAbstractNews(newsItem.content)"
				></p>	
			</li>	
			<li class="text-footer">
				<span>阅读全文</span>
				<!--<yd-navbar-next-icon></yd-navbar-next-icon>-->
				<i class="yd-icon-right"></i>
			</li>
		</ul>
	</dd>
</template>

<script>
	export default {
		name: 'rx-news',
		props:{
			newsItem:{
				type:Object
			}
		},
		methods:{
		
			imgReqPath(val){
				if(''!=val){
					let url = _baseUrl + "/sys/core/file/previewImage.do?fileId=" + val;
					return url;
				}				
			},
			getAbstractNews(content){
				if(content.length>100){	
					let subContent = content.substr(content.indexOf('>')+1,100);
					return subContent + "...";
				}
				return content;
			}
		}
	}
</script>


<style scoped="scoped">
	.text-container{
		background: #fff;
		border-radius: .12rem;
		margin: .2rem;
		padding: 0 .2rem .2rem .2rem;
	}
	
	.text-title h1{
		text-align: center;
		padding: .12rem .2rem;
		width: 100%;
		white-space: nowrap;
		color: #4b4b4b;
		overflow: hidden;
		text-overflow: ellipsis;
		font-size: .35rem;
	}
	
	.text-author{
		display: flex;
		justify-content:space-around;
		font-size: .22rem;
		color: #ccc;
	}
	
	.text-content p,
	.text-img span{
		display: block;
		font-size: .28rem;
		line-height: .3rem;
		/*text-indent: 2em;*/
		color: #666;
		margin: .12rem 0;
	}
	.text-img{
		width: 100%;
	}
	
	.text-img img{
		width: 100%;
		height: auto;
	}
	
	.text-footer{
		border-top: 1px solid #ececec;
		padding-top: .06rem;
	}
	
	.text-footer::after{
		content: '';
		display: block;
		clear: both;
	}
	
	.text-footer span{
		float: left;
		font-size: .24rem;
		color: #999;
	}
	
	.text-footer i{
		float: right;
		font-size: .3rem;
	}
</style>


