<template>
	<div id="box1"> 
		<yd-navbar slot="navbar" title="通讯录">
			<span slot="right" @click="sycnAddr" style="color:#666">同步</span>
		</yd-navbar>
		<div class="headerBar">
			<div id="searchName">
				<h5 id="searchBtn" class="yd-icon-sousuo"></h5>
				<input type="text" id="nameContent" v-model.lazy="searchName" placeholder="搜索联系人" />	
				<div class="clearfix"></div>				
			</div>		
		</div>
		<div id="box2" class="scroll">
			<!--<scroller class="scroller">-->			
				<yd-list theme="4" class="scroller">
					<rx-list-item 
						v-for="addrBook in addrBookList" 
						:key="addrBook.addrId" 
						type='a' 
						img="true" 
						class="addrItem" 
						@click.native="getAddrOperation(addrBook)"
					>
					<div class="message-img">
						<img src="../../../static/img/hy_default.jpg" v-if="addrBook.photoId" />
						<span v-else>{{getFirstLetter(addrBook.name)}}</span>
					</div>
		        	<div class="item-content">
			        <div slot="title" class="item-title">
			          	<span>{{addrBook.name}}</span>
			        </div>
					    <yd-list-other slot="other">
					        <div>
					        	<span class="list-price">手机：{{addrBook.mobile}}</span>
					        </div>
					      </yd-list-other>
		        	</div>
					</rx-list-item>
				</yd-list>
			<!--</scroller>-->
			<div  @touchmove.stop="stop">
				<yd-actionsheet :items="opeartionItem" v-model="showSheet"></yd-actionsheet>
			</div>			
		</div>
		
    	<div class="addUser" @click="addAddr" v-show="showAdd">
    		<i class="yd-icon-addUser"></i>
    	</div>
	</div>		
		
</template>

<script type="text/ecmascript-6">
	import Vue from 'vue';
	import router from '@/router/index';
	import { Icons } from 'vue-ydui/dist/lib.rem/icons';
	import { ListTheme, ListOther } from 'vue-ydui';
	import listItem from '@/components/common/ListItem';
	import { RxUtil } from '@/assets/util.js';
	Vue.component(listItem.name, listItem);

	export default {
		name:'personal',
		data() {
			return {
				addrBookList: [],
				localAddrBook:[],
				searchName: "",
				addrBookLisSize: 0,
				checkAddr: {},
				showSheet: false,
				opeartionItem: [
						{
							label: '打电话',
							callback: () => {
								let phoneNum = this.checkAddr.mobile;
								if(phoneNum == '') {
									this.$dialog.toast({
										mes: '请先添加好友电话..'
									});
									return;
								}
								plus.device.dial(phoneNum, true);
								/* 注意： callback: function() {} 和 callback() {}  这样是无法正常使用当前this的 */
							}
				
						},
						{
							label: '编辑',
							callback: () => {
								let phoneNum = this.checkAddr.mobile;
								if(phoneNum == '') {
									this.$dialog.toast({
										mes: '请先添加好友电话..'
									});
									return;
								}
							}
						}
					]
				
			}
		},
		computed:{
			showAdd:function(){
				return this.$store.state.pageModule.keyboard;
			}
		},
		props:{
			
		},
		watch:{
			
		},
		methods: {
			listAddrBook() {
				var url = _baseUrl +"/mobile/oa/personalAddrBook/listAddrBook.do";
					this.$ajax({
						url: url,
						method: 'POST',
					}).then(response => {
						this.addrBookList = response.data.data;
					}).catch(error => {
						console.log(error);
					});
				},
			searchByName() {
				var url = _baseUrl + "/mobile/oa/personalAddrBook/listAddrBook.do";
				this.$ajax({
					url: url,
					method: 'POST',
					params: {
						search: this.searchName
					}
				}).then(response => {
					this.addrBookList = response.data.data;
					this.addrBookLisSize = response.data.total
				}).catch(error => {
					console.log(error);
				});
			},
			sycnAddr() {
				this.$dialog.confirm({
					title: '同步',
					mes: '是否同步本地通讯录',
					opts: [{
							txt: "确定", //按钮文字
							color: "green", //按钮颜色
							stay: false, //是否保留提示框
							callback: () => {
								this.sync();
							}
						},
						{
							txt: "取消", //按钮文字
							color: false, //按钮颜色
							stay: false, //是否保留提示框							  
						}
					]
				});

			},
			sync() {				
				var self = this;
				self.$dialog.loading.open('正在同步本地通讯录..');
				this.listAddrBook();
				plus.contacts.getAddressBook(plus.contacts.ADDRESSBOOK_PHONE, function(addressbook) {
					addressbook.find(["displayName", "phoneNumbers.mobile"], function(contacts) {
						self.localAddrBook = [];						
						for(var i =0 ; i < 3 ; i++){
							let addrbook = {name:"",mobile:""}; 
							addrbook.name = contacts[i].displayName;
							console.info(JSON.stringify(contacts[i]));
							addrbook.mobile = contacts[i].phoneNumbers[0].value;
							self.localAddrBook.push(addrbook);
										
						}
						self.addrBookList = self.addrBookList.concat(self.localAddrBook);
						//同步后存入数据库
						self.saveAddr(self.addrBookList);
						self.listAddrBook();
						self.$dialog.loading.close();
					}, function() {
						alert("error");
						self.$dialog.loading.close();
					}, {
						multiple: true
					});
				}, function(e) {
					alert("Get address book failed: " + e.message);
					self.$dialog.loading.close();
				});
				
				
			},
			saveAddr(addrBookList){
				var url = _baseUrl +"/mobile/oa/personalAddrBook/saveList.do";
				this.$ajax({
					url:url,
					method:'POST',
					params:{
						data:JSON.stringify(addrBookList)
					}   			
				}).then(response => {
					console.info(JSON.stringify(response.data));
			        this.$dialog.toast({
			            mes: response.data.data ==0?"本次没有数据同步" : "成功同步:" +  response.data.data + "条数据..",
			            timeout: 2000,
			            icon: 'success',
			            
			        });
		    	}).catch(error=> {
					   	console.log(error);
					   	console.log(JSON.stringify(response));
					   	this.$dialog.toast({
			            mes: "同步失败，请稍后进行操作.",
			            timeout: 1000,
			            icon: 'failed',			            
			      		});
		    		});

			},
			getAddrOperation(addrBook){
				this.checkAddr = addrBook
				this.showSheet = true;
			},
			getImageReqPath(fileId){				
				let url = _baseUrl + "/sys/core/file/previewImage.do?fileId=";
				if(fileId!='undefined'||ileId!=''){					
					return url + fileId;
				}
			},
			addAddr(){
				this.$router.push({name:'personalAddrBookAdd'});
				
			},
			getFirstLetter(word){
				return word.substr(0,1).toUpperCase();
			},
			stop(){
				console.log(1111)
			}
			
		},
		created() {
			this.listAddrBook();
		},
		
		watch: {
			searchName: function(val) {
				var self = this;
				var url = _baseUrl + "/mobile/oa/personalAddrBook/listAddrBook.do";
				self.$ajax({
					url: url,
					method: 'POST',
					params: {
						search: val
					}
				}).then(response => {
					self.addrBookList = response.data.data;
					self.addrBookLisSize = response.data.total
				}).catch(error => {
					console.log(error);
				});
			}

		}

	}
</script>

<style scoped>
	#box1{
		height: 100%;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		position: relative;
	}
	
	#box2{
		/*flex-grow: 1;*/
		overflow-x: hidden;
		overflow-y: auto;
		margin-top: .8rem;
		height: calc(100% - 1.8rem);
		position: relative;
		padding-bottom: .4rem;
	}
	
	#scrollView {
		overflow:hidden;
	}
	
	.headerBar{
		height: .8rem;
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 1rem;
		left: 0;
		width: 100%;
		z-index: 1;
		background: #f5f5f5;
	}
	
	#searchName {
		margin: 0.12rem;
		width: calc(100% - .24rem);
		height: 0.6rem;
		border-radius: 0.3rem;
		border: 1px solid #ccc;
		background-color: #fff;
		
	}
	
	.scroller{
		margin-top: 0;
	}
	
	#searchBtn {
		float: left;
		color: #A9A9A9;
		font-size: 0.4rem;
		height: 100%;
		margin-left: 0.1rem;
		line-height: 100%;
		margin-top: 0.1rem;
	}
	#nameContent {
		border-radius: 0.2rem;
		padding-left: 0.1rem;
		width: calc(80% - 0.42rem - 2px);
		float: left;
		height: 100%;
		color: #666;
	}
	
	.message-img{
		overflow: hidden;
	}
	
	.addUser{
		position: absolute;
		right: .4rem;
		bottom: .4rem;
		padding: .1rem;
		opacity: .8;
	}
	
	.addUser i:before{
		color: rgb(13, 170, 246);
		font-size: .8rem;
	}
	
</style>




